import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/project',
      name: 'project',
      component: () => import('@/views/Project/Project.vue')
    },
    {
      path: '/projects',
      name: 'projects',
      component: () => import('@/views/Project/ProjectHome.vue'),
      children: [
        {
          path: '/projects/:id',
          component: () => import('@/views/Project/ProjectPage.vue')
        }
      ]
    },
    {
      path: '/blog',
      name: 'blog',
      component: () => import('@/views/Blog/Blog.vue')
    },
    {
      path: '/blogs',
      name: 'blogs',
      component: () => import('@/views/Blog/BlogHome.vue'),
      children: [
        {
          path: '/blogs/:id',
          component: () => import('@/views/Blog/BlogPage.vue'),
        }
      ]
    },
    {
      path: '/issue',
      name: 'issue',
      component: () => import('@/views/Issue.vue')
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('@/views/About.vue')
    }
  ]
})



