import Vue from 'vue'
import App from './App.vue'
import router from './router'
import $ from 'jquery'
import 'bootstrap/dist/js/bootstrap.min'
import "@/assets/css/bootstrap.min.css"
import "@/assets/css/highlight.min.css"

Vue.config.productionTip = false

new Vue({
  router,
  $,
  render: h => h(App)
}).$mount('#app')



