<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div v-html="projectPage" />
  </div>
</template>

<script>
import "@/assets/css/highlight.min.css";

export default {
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      projectPage: ""
    };
  },
  created() {
    this.getMarkdown();
  },
  watch: {
    $route: function(val) {
      this.getMarkdown();
    }
  },
  methods: {
    getMarkdown: function() {
      try {
        this.projectPage = require("@/assets/markdown/" +
          this.$route.params.id +
          ".md");
      } catch (e) {
        this.projectPage = "404 页面不存在"
      }
    }
  }
};
</script>


